import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ToastrService } from 'ngx-toastr';

import { throwError } from 'rxjs';
import { Client } from 'src/app/client';
import { ClientService } from 'src/app/client.service';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-clientajout',
  templateUrl: './clientajout.component.html',
  styleUrls: ['./clientajout.component.css']
})
export class ClientajoutComponent implements OnInit {
  isError: boolean;
  client:Client;
  private selectedFile2;
  imgURL: any;
  tester:boolean;
   private imageUrl2: any;
  constructor(private http:HttpClient,private route:ActivatedRoute,private router:Router,private clientService:ClientService,private toastr: ToastrService) { 
  this.client=new Client;
 }
  ngOnInit(): void {
 
  }

 
  public onFileChanged2(event) {
    //console.log(event);
    this.selectedFile2 = event.target.files[0];

    let reader = new FileReader();
    reader.readAsDataURL(event.target.files[0]);
    reader.onload = (event2) => {
      this.imageUrl2 = reader.result;
    };

  }
  onSubmit(){
    
    
    const uploadData = new FormData();

  uploadData.append('image', this.selectedFile2, this.selectedFile2.name);


  this.http.post(environment.BACKEND_URL+'/cli/editimageexp', uploadData, { observe:'response' })
  .subscribe((response) => {
   // console.log(response);

    if (response.status === 200) {
     // console.log('yea');
      this.clientService.save(this.client).subscribe(data => {
        this.toastr.success('vous aves ajoute le client');
        this.router.navigate(['/client']);},error=>{ this.toastr.error('username ou email deja existe ');})
      //console.log('Image uploaded successfully');

      
      
      
    } else {
      this.tester=true;
      //console.log('Image not uploaded successfully');
    this.toastr.error("erreur");
    
    }
  }, error => {
    //console.log(error);

    this.isError = true;
    throwError(error);
       this.toastr.error("erreur");
  });
}


  gotoclientlist() {this.router.navigate(['/client']);}

  

}


