import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Editprofil } from 'src/app/models/editprofil';
import { Information } from 'src/app/models/information';
import { Modifierpass } from 'src/app/models/modifierpass';
import { EditprofilService } from 'src/app/services/editprofil.service';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-user-profil',
  templateUrl: './user-profil.component.html',
  styleUrls: ['./user-profil.component.css']
})
export class UserProfilComponent implements OnInit {

   selectedFile2;
 // imgURL: any;
   imageUrl2: any;
  mod:Modifierpass
 information:Information
 // retreiveImg :any;
  Img :any;
   profile :Editprofil;
  constructor(private toastr: ToastrService,private http:HttpClient,private profileSer :EditprofilService,private router: Router) {
    this.profile = new Editprofil();
  this.information=new Information(); 
  this.mod=new Modifierpass();
  this.imageUrl2=null;

}

  ngOnInit(): void {

    this.get();
  }
get(){this.profileSer.info().subscribe(data=>{this.information=data;
//console.log(data);
})}


  public onFileChanged2(event) {
    //console.log(event);
    this.selectedFile2 = event.target.files[0];

    let reader = new FileReader();
    reader.readAsDataURL(event.target.files[0]);
    reader.onload = (event2) => {
      this.imageUrl2 = reader.result;
    };

  }



  editprofile(){
    this.profileSer.editProfile(this.profile).subscribe(
          
      (profile) => {
       
        //this.bookAddedEvent.emit();
        //window.location.reload();
        this.router.navigate(['userprofil']);
      },result=>{this.toastr.success("vous avez changer votre information ");}
    );
    
  }

  editprofilewithimage(){
     
    const uploadData = new FormData();

    uploadData.append('image', this.selectedFile2, this.selectedFile2.name);


    this.http.post(environment.BACKEND_URL +'/user/editimage', uploadData, { observe:'response' })
    .subscribe((response) => {
      console.log(response);

      if (response.status === 200) {
        this.profileSer.editProfile(this.profile).subscribe(
          
          (profile) => {
            
            //this.bookAddedEvent.emit();
            //window.location.reload();
            this.router.navigate(['userprofil']);
          },result=>{this.toastr.success("vous avez changer votre information ");}
        );
        console.log('Image uploaded successfully');
      } else {
        console.log('Image not uploaded successfully');
      }
    }
    );

//  });






 }

 ;

 Submit(){
   this.profileSer.editpass(this.mod).subscribe(result =>{this.toastr.success("vous avez changer votre mot de passe");},err=>{this.toastr.error("erreur")});
 }


   }  