import { Component, OnInit } from '@angular/core';

import { Codecoli } from 'src/app/models/codecoli';
import { Colis } from 'src/app/models/colis';
import { Etatcolis } from 'src/app/models/etatcolis';
import { ColisService } from 'src/app/services/colis.service';

@Component({
  selector: 'app-swivrecolis',
  templateUrl: './swivrecolis.component.html',
  styleUrls: ['./swivrecolis.component.css']
})
export class SwivrecolisComponent implements OnInit {
code:Codecoli;
tablec:Etatcolis[];
test:boolean;
colies:Colis;

items = [

  
  "Audepot",
  "Encours",
  "Rtndepot",
  
  "Retourrecu",
  "Retourdefinitif",
  "Retourexpediteur",
  "livr paye"
  
  ];
  constructor(private colisservice:ColisService) {
    this.code=new Codecoli;
    this.test=false;
    this.colies=new Colis();
    this.tablec=[];
   }

  ngOnInit(): void {
 
  }
  onChercher(f:any){
   // console.log(f.keyword);
    this.test=true;
    this.colisservice.Suivrecolis(f.keyword).subscribe(data =>{this.tablec=data;} );
  }
  onCherche(f:any){
   // console.log(f.keyword);
    this.test=true;
    this.colisservice.chercheparidadmin(f.keyword).subscribe(data=>{this.colies=data;
   this.tablec=data.etatcolis;} );
   //console.log(this.colies);
  }

}
