import { Component, OnInit } from '@angular/core';
import { ChartDataSets, ChartOptions, Chart } from 'chart.js';
import { Color, Label } from 'ng2-charts';
import { HttpClient } from "@angular/common/http";
import { Colis } from 'src/app/models/colis';
import { ColisService } from 'src/app/services/colis.service';
import { Router } from '@angular/router';
import { DashboardadminService } from 'src/app/services/dashboardadmin.service';
import { DatePipe } from '@angular/common';

 interface Datafr {
   month : string  ; 
   value :number; 
 }


@Component({
  selector: 'app-chartadmin',
  templateUrl: './chartadmin.component.html',
  styleUrls: ['./chartadmin.component.css']
})
export class ChartadminComponent implements OnInit {
  datafr :Colis[];
  dateParts:string[];
  dlike : number[]=[];
  fg : string[] =[]; 
  lineChartData: ChartDataSets[] = [ {data: [],label:''}];

  lineChartLabels: Label[]=[];
    
    
    lineChartOptions = {
      responsive: true,
  
    };
  
    lineChartColors: Color[] = [
      {
        borderColor: 'white',
       
      },
    ];
  
    lineChartLegend = true;
    lineChartPlugins = [];
    lineChartType = 'line';
  constructor(public datepipe: DatePipe,private router:Router,private dashboardservice:DashboardadminService,private colisService:ColisService,private http: HttpClient) { 
    this.dlike  =[];
  this.datafr=[]
   this.fg = [];
  }

  
  

   getDatafromBack(){


     return this.dashboardservice.livrepayeadmin().subscribe(data=>{
    
       this.datafr = data ;
        // console.log(data);
       this.datafr.forEach(res=>{
         this.dlike.push(res.fraislivraison);  
         this.fg.push(res.date.toString());
        // console.log(res.date );
       });

        
 

       this.lineChartData = [
        { data: this.dlike, label: 'montantadmin' }, 
      ];
      
      this.lineChartLabels  = this.fg;   

     });
   }

  
  ngOnInit(): void {
    this.getDatafromBack();

    
    //['January', 'February', 'March', 'April', 'May', 'June'];
  
   
  }
}
